@import url(https://fonts.googleapis.com/css?family=Montserrat|Work+Sans&display=swap);
*{margin:0;padding:0;box-sizing:border-box;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}body{font-family:Montserrat;overflow:hidden}nav{height:100vh;width:10vw;padding:1rem 0rem;position:fixed;z-index:3;top:0%;left:0%;display:grid;grid-template-rows:1fr auto;grid-row-gap:.5rem;justify-items:center;align-items:center}nav #nav-label{margin-top:6rem;align-self:center;display:grid;grid-template-columns:1fr;grid-row-gap:.5rem;justify-items:center;align-items:center}nav #nav-label #nav-menu{display:grid;grid-template-columns:1fr;justify-items:center;align-items:center}nav #nav-label #nav-polygon{align-self:flex-end}nav #nav-label h2{margin-top:3rem;font-weight:200;color:#B8BCBE;-webkit-transform:rotate(270deg);transform:rotate(270deg);text-transform:capitalize}nav #nav-arrows{align-self:flex-end;display:grid;grid-template-columns:1fr;justify-items:center;align-items:center;grid-row-gap:.5rem}nav #nav-arrows *{cursor:pointer}

